:root {
  --color-card: #161716;
  --color-text: #fff;
  --color-border: #666;
}

.flipClock {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: center;
  padding-bottom: 20px;
}

.flipUnitContainer {
  display: block;
  position: relative;
  min-width: 160px;
  height: 120px;
  -webkit-perspective-origin: 50% 50%;
  perspective-origin: 50% 50%;
  -webkit-perspective: 300px;
  perspective: 300px;
  background-color: var(--color-card);
  border-radius: 10px;
  box-shadow: 0px 10px 10px -10px grey;
  margin: 0 15px 90px;
}

.flipUnitName {
  font-weight: 600;
  font-size: 24px;
  text-transform: uppercase;
  margin-top: 40px;
}

.upperCard,
.lowerCard {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  width: 100%;
  height: 50%;
  overflow: hidden;
}

.upperCard span,
.lowerCard span {
  font-family: "Major Mono Display", monospace;
  font-size: 110px;
  letter-spacing: -0.2em;
  color: var(--color-text);
  margin-left: -20px;
}

.upperCard {
  -webkit-box-align: end;
  -ms-flex-align: end;
  align-items: flex-end;
  border-bottom: 0.5px solid var(--color-border);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.upperCard span {
  -webkit-transform: translateY(50%);
  transform: translateY(50%);
}

.lowerCard {
  -webkit-box-align: start;
  -ms-flex-align: start;
  align-items: flex-start;
  border-top: 0.5px solid var(--color-border);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
.lowerCard span {
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.flipCard {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  position: absolute;
  left: 0;
  width: 160px;
  height: 60px;
  overflow: hidden;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flipCard span {
  font-family: "Major Mono Display", monospace;
  font-size: 110px;
  letter-spacing: -0.2em;
  color: var(--color-text);
  margin-left: -20px;
}

.flipCard.unfold {
  top: 50%;
  -webkit-box-align: start;
  -ms-flex-align: start;
  align-items: flex-start;
  -webkit-transform-origin: 50% 0%;
  transform-origin: 50% 0%;
  -webkit-transform: rotateX(180deg);
  transform: rotateX(180deg);
  background-color: var(--color-card);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border-top: 0.5px solid var(--color-border);
}
.flipCard.unfold span {
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
.flipCard.fold {
  top: 0%;
  -webkit-box-align: end;
  -ms-flex-align: end;
  align-items: flex-end;
  -webkit-transform-origin: 50% 100%;
  transform-origin: 50% 100%;
  -webkit-transform: rotateX(0deg);
  transform: rotateX(0deg);
  background-color: var(--color-card);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border: 0.5px solid var(--color-border);
  border-bottom: 0.5px solid var(--color-border);
}
.flipCard.fold span {
  -webkit-transform: translateY(50%);
  transform: translateY(50%);
}

.fold {
  -webkit-animation: fold 0.6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1
    normal forwards;
  animation: fold 0.6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal
    forwards;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
}

.unfold {
  -webkit-animation: unfold 0.6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1
    normal forwards;
  animation: unfold 0.6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal
    forwards;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
}

@-webkit-keyframes fold {
  0% {
    -webkit-transform: rotateX(0deg);
    transform: rotateX(0deg);
  }
  100% {
    -webkit-transform: rotateX(-180deg);
    transform: rotateX(-180deg);
  }
}

@keyframes fold {
  0% {
    -webkit-transform: rotateX(0deg);
    transform: rotateX(0deg);
  }
  100% {
    -webkit-transform: rotateX(-180deg);
    transform: rotateX(-180deg);
  }
}
@-webkit-keyframes unfold {
  0% {
    -webkit-transform: rotateX(180deg);
    transform: rotateX(180deg);
  }
  100% {
    -webkit-transform: rotateX(0deg);
    transform: rotateX(0deg);
  }
}
@keyframes unfold {
  0% {
    -webkit-transform: rotateX(180deg);
    transform: rotateX(180deg);
  }
  100% {
    -webkit-transform: rotateX(0deg);
    transform: rotateX(0deg);
  }
}

@media (max-width: 768px) {
  .flipClock {
    padding-bottom: 0px;
  }

  .flipUnitName {
    font-size: 12px;
    text-transform: uppercase;
    margin-top: 10px;
  }

  .flipUnitContainer {
    max-width: 90px;
    height: 60px;
    margin: 0 4px 80px;
    min-width: 90px;
  }

  .upperCard span,
  .lowerCard span {
    font-size: 61px;
    margin-left: -12px;
  }

  .flipCard {
    width: 90px;
    height: 30px;
  }

  .flipCard span {
    font-size: 61px;
    margin-left: -12px;
  }
}
