.l1,
.l2,
.l3,
.l4,
.l5,
.l6,
.l7,
.l8,
.l9,
.l10 {
  width: 80%;
  height: 8%;
  margin-top: 5px;
  background-color: rgba(220, 220, 220, 0.6);
  position: absolute;
}

.l1 {
  top: 0;
  -webkit-animation: moving-1 4000ms linear infinite;
          animation: moving-1 4000ms linear infinite;
}
.l2 {
  top: 10%;
  -webkit-animation: moving-2 4000ms linear infinite;
          animation: moving-2 4000ms linear infinite;
}
.l3 {
  top: 20%;
  -webkit-animation: moving-1 3000ms linear infinite;
          animation: moving-1 3000ms linear infinite;
}
.l4 {
  top: 30%;
  -webkit-animation: moving-2 6000ms linear infinite;
          animation: moving-2 6000ms linear infinite;
}
.l5 {
  top: 40%;
  -webkit-animation: moving-1 4000ms linear infinite;
          animation: moving-1 4000ms linear infinite;
}
.l6 {
  top: 50%;
  -webkit-animation: moving-2 4000ms linear infinite;
          animation: moving-2 4000ms linear infinite;
}
.l7 {
  top: 60%;
  -webkit-animation: moving-1 2000ms linear infinite;
          animation: moving-1 2000ms linear infinite;
}
.l8 {
  top: 70%;
  -webkit-animation: moving-2 4000ms linear infinite;
          animation: moving-2 4000ms linear infinite;
}
.l9 {
  top: 80%;
  -webkit-animation: moving-1 3000ms linear infinite;
          animation: moving-1 3000ms linear infinite;
}
.l10 {
  top: 90%;
  -webkit-animation: moving-2 5000ms linear infinite;
          animation: moving-2 5000ms linear infinite;
}

@-webkit-keyframes moving-1 {
  from {
    -webkit-transform: translateX(-5%);
            transform: translateX(-5%);
  }
  50% {
    -webkit-transform: translateX(30%);
            transform: translateX(30%);
  }
  to {
    -webkit-transform: translateX(-5%);
            transform: translateX(-5%);
  }
}

@keyframes moving-1 {
  from {
    -webkit-transform: translateX(-5%);
            transform: translateX(-5%);
  }
  50% {
    -webkit-transform: translateX(30%);
            transform: translateX(30%);
  }
  to {
    -webkit-transform: translateX(-5%);
            transform: translateX(-5%);
  }
}

@-webkit-keyframes moving-2 {
  from {
    -webkit-transform: translateX(30%);
            transform: translateX(30%);
  }
  50% {
    -webkit-transform: translateX(-5%);
            transform: translateX(-5%);
  }
  to {
    -webkit-transform: translateX(30%);
            transform: translateX(30%);
  }
}

@keyframes moving-2 {
  from {
    -webkit-transform: translateX(30%);
            transform: translateX(30%);
  }
  50% {
    -webkit-transform: translateX(-5%);
            transform: translateX(-5%);
  }
  to {
    -webkit-transform: translateX(30%);
            transform: translateX(30%);
  }
}

:root {
  --color-card: #161716;
  --color-text: #fff;
  --color-border: #666;
}

.flipClock {
  display: flex;
  justify-content: center;
  padding-bottom: 20px;
}

.flipUnitContainer {
  display: block;
  position: relative;
  min-width: 160px;
  height: 120px;
  -webkit-perspective-origin: 50% 50%;
  perspective-origin: 50% 50%;
  -webkit-perspective: 300px;
  perspective: 300px;
  background-color: #161716;
  background-color: var(--color-card);
  border-radius: 10px;
  box-shadow: 0px 10px 10px -10px grey;
  margin: 0 15px 90px;
}

.flipUnitName {
  font-weight: 600;
  font-size: 24px;
  text-transform: uppercase;
  margin-top: 40px;
}

.upperCard,
.lowerCard {
  display: flex;
  position: relative;
  justify-content: center;
  width: 100%;
  height: 50%;
  overflow: hidden;
}

.upperCard span,
.lowerCard span {
  font-family: "Major Mono Display", monospace;
  font-size: 110px;
  letter-spacing: -0.2em;
  color: #fff;
  color: var(--color-text);
  margin-left: -20px;
}

.upperCard {
  align-items: flex-end;
  border-bottom: 0.5px solid #666;
  border-bottom: 0.5px solid var(--color-border);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.upperCard span {
  -webkit-transform: translateY(50%);
  transform: translateY(50%);
}

.lowerCard {
  align-items: flex-start;
  border-top: 0.5px solid #666;
  border-top: 0.5px solid var(--color-border);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
.lowerCard span {
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.flipCard {
  display: flex;
  justify-content: center;
  position: absolute;
  left: 0;
  width: 160px;
  height: 60px;
  overflow: hidden;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flipCard span {
  font-family: "Major Mono Display", monospace;
  font-size: 110px;
  letter-spacing: -0.2em;
  color: #fff;
  color: var(--color-text);
  margin-left: -20px;
}

.flipCard.unfold {
  top: 50%;
  align-items: flex-start;
  -webkit-transform-origin: 50% 0%;
  transform-origin: 50% 0%;
  -webkit-transform: rotateX(180deg);
  transform: rotateX(180deg);
  background-color: #161716;
  background-color: var(--color-card);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border-top: 0.5px solid #666;
  border-top: 0.5px solid var(--color-border);
}
.flipCard.unfold span {
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
.flipCard.fold {
  top: 0%;
  align-items: flex-end;
  -webkit-transform-origin: 50% 100%;
  transform-origin: 50% 100%;
  -webkit-transform: rotateX(0deg);
  transform: rotateX(0deg);
  background-color: #161716;
  background-color: var(--color-card);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border: 0.5px solid #666;
  border: 0.5px solid var(--color-border);
  border-bottom: 0.5px solid #666;
  border-bottom: 0.5px solid var(--color-border);
}
.flipCard.fold span {
  -webkit-transform: translateY(50%);
  transform: translateY(50%);
}

.fold {
  -webkit-animation: fold 0.6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1
    normal forwards;
  animation: fold 0.6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal
    forwards;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
}

.unfold {
  -webkit-animation: unfold 0.6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1
    normal forwards;
  animation: unfold 0.6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal
    forwards;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
}

@-webkit-keyframes fold {
  0% {
    -webkit-transform: rotateX(0deg);
    transform: rotateX(0deg);
  }
  100% {
    -webkit-transform: rotateX(-180deg);
    transform: rotateX(-180deg);
  }
}

@keyframes fold {
  0% {
    -webkit-transform: rotateX(0deg);
    transform: rotateX(0deg);
  }
  100% {
    -webkit-transform: rotateX(-180deg);
    transform: rotateX(-180deg);
  }
}
@-webkit-keyframes unfold {
  0% {
    -webkit-transform: rotateX(180deg);
    transform: rotateX(180deg);
  }
  100% {
    -webkit-transform: rotateX(0deg);
    transform: rotateX(0deg);
  }
}
@keyframes unfold {
  0% {
    -webkit-transform: rotateX(180deg);
    transform: rotateX(180deg);
  }
  100% {
    -webkit-transform: rotateX(0deg);
    transform: rotateX(0deg);
  }
}

